<template>
    <!-- :title="`免费注册`"  -->
    <el-dialog class="custom" width="900px" :modal="false" :visible.sync="isOpen"
        :before-close="closed" :close-on-click-modal="false" v-dialogDrag >
        <Register @close="success"/>
    </el-dialog>
</template>

<script>
import Register from './Register'
export default {
    components: { Register },
    props: {
        'title': { default: '未命名' },
        'open': { default: false },
    },
    watch: {
        'open' (v) {
            this.isOpen = v
        },
    },
    methods: {
        success () {
            this.isOpen = false
            this.$emit('success', true)
        },
        closed () {
            this.isOpen = false
            this.$emit('close', true)
        }
    },
    data () {
        return {
            isOpen: false
        }
    }
}
</script>

