<template>
	<div class="wrap" v-loading="isLoading">
		
		<div class="register">
			<div class="form">
				<!-- <p>{{ $dialogAlert.isOpen }}, {{ $dialogAlert.message }}</p> -->
				<ul>
					<li><input type="text" ref="name" v-model="formData.name" placeholder="请输入用户名"></li>
					<li><input type="text" ref="contacts" v-model="formData.contacts" placeholder="请输入联系人"></li>
					<li><input type="text" ref="phone" v-model="formData.phone" placeholder="请输入电话"></li>
					<li><input type="text" ref="address" v-model="formData.address" placeholder="请输入地址"></li>
					<li><input type="text" ref="account" v-model="formData.account" placeholder="请输入账号"></li>
					<li><input type="password" ref="password" v-model="formData.password" placeholder="请输入密码"></li>
				</ul>
			</div>
			<div class="buttons">
				<a href="javascript:;" @click="Submit">提交</a>
				<!-- <a href="javascript:;" v-route-to="`/login`" class="small">返回登录</a> -->
			</div>
		</div>

		<DialogAlert :open="$dialogAlert.isOpen" :message="$dialogAlert.message" @close="closeAlert" />

	</div>
</template>
<script>
import Cookie from 'vue-cookie'
import DialogAlert from '@src/components/dialog.alert/dialog.alert'

export default {
	components: { DialogAlert },
	methods: {

        closeAlert () {
            this.$refs[this.$dialogAlert.ref].focus()
            this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: false, message: '', ref: '' })
            this.$forceUpdate()
        },

        Login (formData) {
            return new Promise(resolve => {
                this.axios.post('/anno/login', formData).then(rs => {
                    if (rs.data.code == 0) {
                        Cookie.set('UserInfo', JSON.stringify(rs.data.data))
                        Cookie.set('token', true)
                    }
                    resolve(rs)
                })
            })
		},

		Submit () {

			if (!this.formData.name) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
                    message: '请填写用户名', ref: 'name' })
                this.$forceUpdate();
                return false;
			}
			if (!this.formData.contacts) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
					message: '请填写联系人', ref: 'contacts' })
				this.$forceUpdate(); return false;
			}

			if (!this.formData.phone) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
					message: '请填写电话', ref: 'phone' })
				this.$forceUpdate(); return false;
			}
			if (!this.formData.address) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
					message: '请填写地址', ref: 'name' })
				this.$forceUpdate(); return false;
			}
			if (!this.formData.account) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
					message: '请填写账号', ref: 'account' })
				this.$forceUpdate(); return false;
			}
			if (!this.formData.password) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
					message: '请填写密码', ref: 'password' })
				this.$forceUpdate(); return false;
			}

			this.isLoading = true
            // this.axios.post('/sys/saveUser', this.formData,
            this.axios.post('/software/saveUser', this.formData).then(rs => {
				this.isLoading = false
				if (rs.data.code == 0) {
					// this.$router.push('/login')
					// Cookie.set('UserInfo', JSON.stringify({ account: this.formData.account }))
                    
                    this.Login(this.formData).then(rs => {
                        setTimeout(() => {
                            this.$emit('close', true)
                            this.$message({ type: 'success', message: '注册成功' });
                        }, 500)
                    })

				} else {
					this.$message({ type: 'error', message: rs.data.msg });
				}
			})

			// this.axios('/mould/selectList', this.formData).then(rs => {
			// 	console.log(rs.data)
			// })
		}
    },
	data () {
		return {
			alertOpen: false, alertMessage: '',
			//
			isLoading: false,
			formData: {
				// name: "",  account: "", password: "", contacts: "", phone: "", address: ""
				"name":"昵称1", 
				"account":"16728493041",
				"password":"123456",  
				"contacts":"联系人2",  
				"phone":"电话2",
				"address":"地址2"
			}
		}
	}
}
</script>
<style lang="less" scoped>
@import url('./Register.less');
</style>